// 拿到vuex中的函数
import {mapGetters, mapMutations} from 'vuex'
// 检测是否登录、token是否过期
import {isLogin} from '@/utils/utils.js'
// toast组件，拿到的不是组件，是方法；
// 如此这般：Toast('我是提示文案，建议不超过十五字~');
import Toast from '../../wxcomponents/weapp/dist/toast/toast'
import Dialog from '../../wxcomponents/weapp/dist/dialog/dialog'
export default {
	data () {
		return {
			watchIsLogin:false
		}
	},
	onShow() {
		this.watchIsLogin = isLogin()
		console.log(this.watchIsLogin)
	},
	methods: {
		logOut() {
			Dialog.confirm({
			  title: '确认退出登录',
			  message: '退出登录后您将无法使用此账号登录智慧食堂小程序'
			}).then(() => {
			  // on confirm
			  console.log('logOut')
			  this.setToken('')
			  this.setTokenExpiration('')
			  this.setUserInfo({
			  	avatarUrl: null,
			  	city: null,
			  	country: null,
			  	gender: null,
			  	language: null,
			  	nickName: null,
			  	province: null
			  })
			  this.setUserInfoData({
			  	address: null, // 用户地址
			  	avatarUrl: null, // 头像imgURL
			  	birth: null, // 生日
			  	city: null, // 城市
			  	country: null, // 国家
			  	createTime: null, // 注册时间
			  	dataSort: null, // 数据排序
			  	email: null, // 邮箱
			  	gender: null, // 性别 1 | 0
			  	id: null, // 用户ID
			  	idCard: null, // 当用户选择内部员工时必填
			  	isDelete: null, // 逻辑删除: 0:未删除 1:已删除
			  	language: null, // 语言
			  	mobile: null, // 手机号
			  	nickName: null, // 昵称
			  	openId: null, // string
			  	passwd: null, // 登录密码
			  	province: null, // 省份
			  	sessionKey: null, // 
			  	status: null, // 数据状态 0:正常  1:锁定
			  	unionId: null, // 
			  	updateTime: null, // 更新时间: 2020-11-11 00:00:00
			  	userType: null, // 用户类型 0:内部 1:外部 当外部用户时, 不需要绑定员工号
			  })
			  this.watchIsLogin = false
			  Toast('已退出登录')
			}).catch(() => {
			  // on cancel
			});
			
			
			
			// 退出登录后，如果需要跳转到登录页，开启下方注释即可
			// uni.navigateTo({
			// 	// 这里的logOut代表是否是退出操作，如果是退出操作，那么跳转页面的时候，传参就是1
			// 	url: '../../pages/login/login?logOut='+'1',
			// 	complete (res) {
			// 		console.log(res)
			// 	}
			// })
		},
		openLogin() {
			uni.navigateTo({
				url: '../../pages/login/login',
				complete (res) {
					console.log(res)
				}
			})
		},
		
		// 因为ul下的4个li 都是跳转页面，而且都需要判断用户是否已经登录
		// 那么这里就统一进行跳转，
		// 根据传过来的 参数 进行页面跳转
		toPage(pageCode) {
			
			if(this.watchIsLogin) {
				// 1 个人信息页
				// 2 我的地址
				// 3 意见反馈
				// 4 修改手机号
				switch(pageCode){
					case 1:
						this.openPage('me_userInfo');
						break;
					case 2:
						this.openPage('me_faceUpLoad');
						break;
					case 3:
						this.openPage('me_userAddrs');
						break;
					case 4:
						this.openPage('me_feedback'); 
						break;
					case 5:
						this.openPage('me_editUserPhone'); 
						break;
				}
				
			} else {
				Toast('请您先登录')
				console.log('用户未登录')
			}
		},
		openPage(pageNmae) {
			uni.navigateTo({
				url: '../../pages/'+pageNmae+'/'+pageNmae,
				complete (res) {
					console.log(res)
				}
			})
		},
		
		// 余额
		openBalance() {
			if(this.watchIsLogin) {
				uni.navigateTo({
					url: '../../pages/me_balance/me_balance',
					complete (res) {
						console.log(res)
					}
				})
			} else {
				Toast('请您先登录')
				console.log('用户未登录')
			}
			
		},
		// 充值
		openRecharge() {
			if(this.watchIsLogin) {
				uni.navigateTo({
					url: '../../pages/me_recharge/me_recharge',
					complete (res) {
						console.log(res)
					}
				})
			} else {
				Toast('请您先登录')
				console.log('用户未登录')
			}
		},
		
		...mapMutations({
			// 这里映射了这个方法，那么在调用x的时候，就等于使用了this.$store.commit('SET_SINGER', value)这个方法；
			setToken: 'setToken',
			setTokenExpiration: 'setTokenExpiration',
			setUserPhone: 'setUserPhone',
			setUserInfo: 'setUserInfo',
			setUserInfoData: 'setUserInfoData'
		}),
	},
	computed: {
		// vuex提供的辅助函数，拿到store/getters.js向外暴露的内容；
		...mapGetters([
			'store_UserInfo',
			'store_UserInfoData', // 用户详细数据
			'store_token',
			'store_tokenExpiration'
		])
	}
}
